.header {
  display: flex;
  width: 100%;
}
.header__wrapper {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.header__logo {
  display: flex;
  justify-content: center;
}
.header__menu-mobile {
  display: none;
}
.main {
  width: 100%;
  position: relative;
  justify-content: center;
  flex-direction: column;
}
.main-background {
  width: 90%;
  margin: 20px 15px;
}
.main-description {
  width: 90%;
  margin: 20px 15px;
  justify-content: center;
  display: flex;
  align-self: center;
}
.main__city-info {
  position: relative;
  width: 100%;
  display: flex;
  justify-content: center;
}
.main__text {
  width: 285px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.main__text {
  position: absolute;
  top: 0;
  left: 0;
}
.main__city-background {
  width: 100%;
  display: flex;
  justify-content: center;
}
.city-2 {
  width: 90%;
  display: flex;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
}
.city-2__discribes {
  margin-top: 30px;
  display: flex;
  justify-content: center;
  flex-direction: column;
}
.city-2__advertising {
  display: flex;
  justify-content: center;
  align-items: center;
}
.city-2__house-text {
  display: flex;
}
.city-2__text-element {
  display: flex;
  margin-top: -24px;
}
.city-2__block {
  margin-left: 20px;
  display: flex;
  flex-direction: column;
}
.icon-house {
  display: flex;
  align-self: flex-start;
}
.icon-gift {
  display: flex;
  align-self: flex-start;
}
.icon-burger {
  display: flex;
  align-self: flex-start;
}
body {
  width: 100%;
  background: #ffffff;
}
